:host {
    --dock-background-color: rgb(var(--color-glaucous-darker));
    --dock-item-text-color: rgb(var(--color-cyan-lighter));
    --dock-item-text-color--selected: rgb(var(--color-white));
    --dock-item-icon-color: rgb(var(--color-cyan-lighter));
    --dock-item-background-color--selected: rgb(var(--color-cyan-darker));
}

.application {
    background-color: rgb(var(--contrast-1600));
    overflow: hidden;
    border-radius: 0.5rem;
    height: 30rem;
}
